<template>
  <div>
    <!-- 帅选条件 -->
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->
        <el-form-item label="产品名称">
          <el-input
            v-model="listQuery.product_name"
            placeholder="请输入"
          ></el-input>
        </el-form-item>
        <el-form-item label="规格型号">
          <el-input v-model="listQuery.rule" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="状态">
          <el-select
            v-model="listQuery.product_status"
            placeholder="请选择"
            style="width: 100%"
          >
            <el-option label="启用" value="启用"></el-option>
            <el-option label="禁用" value="禁用"></el-option>
          </el-select>
        </el-form-item>

        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查询</el-button
          >
          <el-button size="medium" @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 表格 -->
    <el-card class="box-card mt20">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span>人员列表</span>
      </div> -->
      <div class="">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item>
            <el-button type="primary" size="medium" plain @click="addBtn"
              ><i class="el-icon-plus"></i> 新增</el-button
            >
          </el-form-item>
        </el-form>
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="ispagination"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>

    <!-- 新增物料弹出层 -->
    <el-dialog
      :title="dialogTitle"
      width="1000px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="100px"
      >
        <el-row>
          <p class="dialog-title">基本信息</p>

          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="产品名称" prop="product_name">
                <el-input v-model="dialogForm.product_name"></el-input>
              </el-form-item>
              <el-form-item label="状态" prop="product_status">
                <el-select
                  v-model="dialogForm.product_status"
                  placeholder="请选择"
                  style="width: 100%"
                >
                  <el-option label="启用" value="启用"></el-option>
                  <el-option label="禁用" value="禁用"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="产品编号" prop="product_id">
                <el-input v-model="dialogForm.product_id" :readonly="productIdReadonly"></el-input>
              </el-form-item>
              <el-form-item label="规格型号" prop="rule">
                <el-input v-model="dialogForm.rule"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-form-item label="备注" prop="description">
            <el-input
              type="textarea"
              v-model="dialogForm.description"
            ></el-input>
          </el-form-item>
          <p class="dialog-title">物料明细</p>
          <el-button type="primary" size="small" plain @click="AddListRow()"
            >添加物料</el-button
          >
          <el-table
            :data="dialogForm.response_datas"
            style="width: 100%"
            class="dialogTable"
            height="240"
          >
            <el-table-column fixed="left" label="物料名称" align="center">
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.matter_name'"
                >
                  <el-input v-model="scope.row.matter_name"></el-input>
                </el-form-item>
              </template>
            </el-table-column>
            <el-table-column label="规格型号" align="center">
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.rule'"
                >
                  <el-input v-model="scope.row.rule"></el-input>
                </el-form-item>
              </template>
            </el-table-column>
            <el-table-column label="类型" align="center">
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.matter_category'"
                >
                      <el-select v-model="scope.row.matter_category" placeholder="请选择">
                        <el-option label="A类" value="A类"></el-option>
                        <el-option label="B类" value="B类"></el-option>
                        <el-option label="其他" value="其他"></el-option>
                      </el-select>
                </el-form-item>
              </template>
            </el-table-column>
            <el-table-column label="数量" align="center">
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.matter_usage'"
                  :rules="[
                    { required: true, message: '数量不能为空' },
                    { type: 'number', message: '数量必须为数值' },
                  ]"
                >
                  <el-input v-model.number="scope.row.matter_usage"></el-input>
                </el-form-item>
              </template>
            </el-table-column>

            <el-table-column label="操作" width="80" align="center">
              <template slot-scope="scope">
                <el-form-item>
                  <el-button
                    size="small"
                    type="danger"
                    plain
                    @click="handleDelete(scope.$index, scope.row)"
                    >删除</el-button
                  >
                </el-form-item>
              </template>
            </el-table-column>
          </el-table>
        </el-row>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import qs from "qs";

export default {
  name: "ProjectBOM",
  components: {
    ELTable,
  },
  data() {
    return {
      
      // 筛选
      listQuery: {
        product_name: "",
        rule: "",
        product_status: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        product_name: "",
        rule: "",
        product_status: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      ispagination: true, // 分页是否显示
      // 新增人员弹出层
      productIdReadonly: false, 
      dialogTitle: "",
      dialogFlag: false,
      dialogForm: {
        prodect_code: "",
        product_name: "",
        product_id: "",
        rule: "",
        product_status: "",
        description: "",
        response_datas: [
          {
            matter_name: "",
            rule: "",
            matter_category: "",
            matter_usage: "",
          },
        ],
      },
      dialogFormBase: {
        prodect_code: "",
        product_name: "",
        product_id: "",
        rule: "",
        product_status: "",
        description: "",
        response_datas: [
          {
            matter_name: "",
            rule: "",
            matter_category: "",
            matter_usage: "",
          },
        ],
      },
      dialogFormRules: {
        product_name: [
          { required: true, message: "请输入产品名称", trigger: "blur" },
        ],
        product_id: [
          { required: true, message: "请输入产品编号", trigger: "blur" },
          { min: 3, max: 18, message: "长度为3~18个字节!", trigger: "blur" },
          {
            trigger: "blur",
            validator: (rule, value, callback) => {
              var productId = /^[a-z]+$/;
              console.log(productId.test(value));
              if (!productId.test(value)) {
                callback(
                  new Error(
                    "仅允许小写字母!"
                  )
                );
              } else {
                callback();
              }
            },
          },
        ],
      },

      // 表格列
      tableCol: [
        {
          label: "产品内码",
          field: "prodect_code",
          align: "center",
        },
        {
          label: "产品名称",
          field: "product_name",
          align: "center",
        },
        {
          label: "产品编号",
          field: "product_id",
          align: "center",
        },
        {
          label: "规格型号",
          field: "rule",
          align: "center",
        },
        {
          label: "状态",
          field: "product_status",
          align: "center",
        },
        {
          label: "备注",
          field: "description",
          align: "center",
        },

        {
          label: "操作",
          colType: "solt",
          align: "center",
          option: [
            {
              label: "修改",
              event: this.colEditBtn,
            },
            {
              label: "删除",
              event: this.colDeleteBtn,
            },
          ],
        },
      ],
      tableData: [],
    };
  },
  methods: {
    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticBOMProductList(this.listQuery);
    },
    // 重置
    resetForm() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticBOMProductList(this.listQuery);
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticBOMProductList(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticBOMProductList(this.listQuery);
    },

    // 产品结构 查询
    getLogisticBOMProductList(params) {
      baseManagement.getLogisticBOMProductList(params).then((res) => {
        console.log("产品结构 查询", res);
        if (res.code == 0) {
          this.tableData = res.data.data;
          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 产品结构 新增
    postLogisticBOMProductList(params) {
      baseManagement.postLogisticBOMProductList(params).then((res) => {
        console.log("产品结构 新增", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticBOMProductList(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 产品结构 修改
    colEditBtn(rowData) {
      console.log(rowData);
      this.dialogTitle = "修改产品";
      this.dialogFlag = true;
      this.productIdReadonly = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = {
        prodect_code: rowData.prodect_code,
        product_id: rowData.product_id,
        product_name: rowData.product_name,
        rule: rowData.rule,
        product_status: rowData.product_status,
        description: rowData.description,
        response_datas: rowData.response_datas,
      };
    },

    getLogisticPutBOMProductList(params) {
      baseManagement.getLogisticPutBOMProductList(params).then((res) => {
        console.log("修改产品 修改", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticBOMProductList(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 新增
    addBtn() {
      this.dialogTitle = "新增产品";
      this.dialogFlag = true;
      this.productIdReadonly = false;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = JSON.parse(JSON.stringify(this.dialogFormBase));
    },
    //点击加号添加一行数据
    AddListRow() {
      this.dialogForm.response_datas.push({
        matter_name: "",
        rule: "",
        matter_category: "",
        matter_usage: "",
      });
    },
    // 弹出层确定
    dialogSave() {
      this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          if (this.dialogTitle == "新增产品") {
            console.log("新增产品", this.dialogForm);
            this.postLogisticBOMProductList(this.dialogForm);
          } else {
            console.log("修改产品", this.dialogForm);
            this.dialogForm.response_datas = JSON.stringify(
              this.dialogForm.response_datas
            );
            console.log("this.dialogForm.response_datas",this.dialogForm.response_datas);

            this.getLogisticPutBOMProductList(this.dialogForm);
          }
        }
      });
    },

    // 产品结构 删除
    getLogisticDeleteBOMProductList(params) {
      baseManagement.getLogisticDeleteBOMProductList(params).then((res) => {
        console.log("产品结构 删除", res);
        if (res.code == 0) {
          this.getLogisticBOMProductList(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 点击删除当前行
    handleDelete(index, rowData) {
      if (rowData.bom_matter_code) {
        this.$confirm("确定执行删除操作吗？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }).then(() => {
          let params = {
            prodect_code: rowData.prodect_code,
            response_datas: JSON.stringify([
              { bom_matter_code: rowData.bom_matter_code },
            ]),
          };
          this.getLogisticDeleteBOMProductList(params);
          this.dialogForm.response_datas.splice(index, 1);
        });
      } else {
        this.dialogForm.response_datas.splice(index, 1);
      }
    },
    colDeleteBtn(rowData) {
      this.$confirm("确定执行删除操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        let params = {
          prodect_code: rowData.prodect_code,
          response_datas: JSON.stringify([]),
        };
        this.getLogisticDeleteBOMProductList(params);
      });
    },
  },
  mounted() {
    // 产品结构 查询
    this.getLogisticBOMProductList(this.listQuery);
  },
};
</script>
<style lang="scss" scoped>
.dialogTable {
  .el-form-item {
    // margin: 0;
    /deep/ .el-form-item__content {
      margin: 0 !important;
    }
  }
}
</style>